import styled from "styled-components";

const kp_blue = "#003368";
const kp_red = "#FF1E00";

export const FormTitle = styled.h1`
  align-self: ${({ left }) => (left ? "flex-start" : "flex-end")};
  color: black;
  margin: 0 10%;
  font-size: 2.5rem;
  position: relative;
  text-align: left;
  padding: 1% 0;

  &::after {
    content: "";
    width: 50%;
    height: 10px;
    background: linear-gradient(-90deg, ${kp_blue}, ${kp_red}), ${kp_red};
    position: absolute;
    top: 0;
    ${({ left }) => (left ? "left: 0" : "right: 0")};
    border-radius: 10px;
  }

  @media screen and (max-width: 800px) {
    font-size: 1.5rem;
    padding: 3% 0;
    font-weight: bold;

    &::after {
      height: 8px;
    }
  }
`;

export const FormSectionContainer = styled.section`
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 100%;
  min-height: 80vh;
  padding-bottom: 5%;
`;

export const FormElement = styled.div`
  width: 45%;
  min-height: 50vh;
  padding: 3% 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: ${({ left }) => (left ? "flex-start" : "flex-end")};

  @media screen and (max-width: 800px) {
    width: 100%;
    align-items: center;
  }
`;
export const FormElementContainer = styled.div`
  width: 60%;
  min-height: 50vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 4% 0;
  flex-wrap: wrap;

  @media screen and (max-width: 800px) {
    width: 80%;
  }
`;

export const FormImage = styled.img`
  width: 25%;

  @media screen and (max-width: 800px) {
    display: none;
  }
`;

export const FormInput = styled.input`
  width: 100%;
  border: ${({ error }) =>
    error ? `1px solid ${kp_red}` : "1px solid rgba(0, 51, 104, 0.2)"};
  outline: none;
  border-radius: 5px;
  height: 4vh;
  line-height: 4vh;
  font-size: 1.1rem;
  background: transparent;
  margin: 5px 0 0 0;
  transition: 0.1s ease-in;
  padding: 3%;

  &:focus {
    border: 1px solid rgba(0, 51, 104, 1);
  }
`;

export const FormLabel = styled.span`
  width: 50%;
  font-size: 1.1rem;
  font-weight: bold;
  padding-left: 1%;
`;

export const FormError = styled.span`
  width: 100%;
  margin-top: 5px;
  color: ${kp_red};
  padding-left: 1%;
  font-size: 0.8rem;
  font-weight: bold;
  display: ${({ error }) => (error ? "block" : "none")};
`;

export const FormInputContainer = styled.div`
  width: 60%;
  min-height: 8vh;
  margin: 1% 0;

  @media screen and (max-width: 800px) {
    width: 70%;
  }
`;

export const FormSubmit = styled.input`
  width: 232px;
  height: 52px;
  background: #f6f8f8;
  color: black;
  font-weight: bold;
  text-transform: uppercase;
  outline: none;
  border: 0;
  cursor: pointer;
  transition: 0.1s ease-in;
  font-size: 1.6rem;

  &:hover {
    color: rgba(255, 255, 255, 0.9);
    background: linear-gradient(-90deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.2)),
      linear-gradient(-90deg, ${kp_blue}, ${kp_red});
  }
  @media screen and (max-width: 800px) {
    width: 184px;
    height: 40px;
    font-size: 1.2rem;
  }
`;

export const BorderDiv = styled.div`
  width: 240px;
  height: 60px;
  background: linear-gradient(-90deg, ${kp_blue}, ${kp_red});
  display: flex;
  justify-content: center;
  align-items: center;

  @media screen and (max-width: 800px) {
    width: 192px;
    height: 48px;
  }
`;
