import styled from "styled-components";
import { Link } from "react-router-dom";
import { GiHamburgerMenu } from "react-icons/gi";

const kp_blue = "#003368";
const kp_red = "#FF1E00";
// const bg_blue = "#007EC3";

export const NavContainer = styled.section`
  position: fixed;
  z-index: 50;
  top: 0;
  width: 100%;
  /* background: ${({ background }) =>
    background ? "#f6f8f8" : "transparent"}; */

  background: #f6f8f8;
  box-shadow: ${({ background }) =>
    background ? "0px 0px 5px rgba(0,0,0,0.3)" : "transparent"};
  transition: 0.3s ease-in;
`;

export const Nav = styled.nav`
  width: 100%;
  background: transparent;
  height: 10vh;
  display: flex;
  align-items: center;
  padding: 1rem;
  justify-content: space-between;
`;

export const NavLogo = styled.div`
  font-weight: bold;
  text-transform: uppercase;
  font-size: 2rem;
  width: 200px;
  line-height: 1;
  color: ${kp_red};

  @media screen and (max-width: 800px) {
    font-size: 1.5rem;
  }
`;

export const LinkList = styled.ul`
  list-style-type: none;
  display: flex;
  width: 40%;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;

  @media screen and (max-width: 800px) {
    display: none;
  }
`;

export const NavLink = styled(Link)`
  color: rgba(0, 54, 101, 0.8);
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: bold;
  text-transform: uppercase;
  transition: 0.2s ease-in;
  text-align: center;
  padding: 1% 3%;
  border-radius: 4px;
  position: relative;

  &:hover {
    /* /* background: ${kp_blue}; */
    color: rgba(0, 54, 101, 1);

    &::after {
      width: 60%;
    }
  }

  &::after {
    content: "";

    height: 1px;
    background: ${kp_blue};
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 10%;
    /* border-radius: 3px; */
    transition: 0.15s ease-in;
    width: 0%;
  }

  @media screen and (max-width: 800px) {
    width: 100%;

    &:hover {
      background: transparent;
      color: ${kp_blue};
    }
  }
`;

export const NavText = styled.p`
  margin: 0;
  padding: 0;
  color: ${kp_blue};
  font-size: 1.15rem;

  @media screen and (max-width: 800px) {
    display: none;
  }
`;

export const Dropdown = styled.div`
  width: 100%;
  height: ${({ open }) => (!open ? "0" : "40vh")};
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  overflow: hidden;
  transition: 0.5s ease-in-out;
`;

export const HamburgerIcon = styled(GiHamburgerMenu)`
  color: ${kp_blue};
  font-size: 2rem;
`;
