import {
  ImageCarousel,
  LeftIcon,
  RightIcon,
  Slide,
  SliderContainer,
  SlideSubtitle,
  SlideTitle,
} from "./HeroElements";
import Img1 from "../../Img/img1.jpg";
import Img2 from "../../Img/img2.jpg";
import Img3 from "../../Img/img3.jpg";
import Img4 from "../../Img/img4.jpg";
import Img5 from "../../Img/img5.jpg";
import { useState, useRef, useEffect } from "react";

function useInterval(callback, delay) {
  const intervalRef = useRef();
  const callbackRef = useRef(callback);

  useEffect(() => {
    callbackRef.current = callback;
  }, [callback]);

  useEffect(() => {
    if (typeof delay === "number") {
      intervalRef.current = window.setInterval(
        () => callbackRef.current(),
        delay
      );
      return () => window.clearInterval(intervalRef.current);
    }
  }, [delay]);

  return intervalRef;
}

const Hero = () => {
  const [offset, setOffset] = useState(-100);
  const [transition, setTransition] = useState("0.5s ease-in-out");
  const slideCount = 5;

  const offsetChanger = (left = true) => {
    let off = offset;
    let znak = left ? -1 : 1;

    off += 100 * znak;

    if (off <= -((slideCount + 1) * 100)) {
      window.setTimeout(() => {
        setTransition("none");
        setOffset(-100);
      }, 500);
    } else if (off >= 0) {
      window.setTimeout(() => {
        setTransition("none");
        setOffset(-(slideCount * 100));
      }, 500);
    }

    setTransition("0.5s ease-in-out");
    setOffset(off);
  };

  const intervalRef = useInterval(() => {
    offsetChanger();
  }, 5000);

  return (
    <ImageCarousel>
      <LeftIcon
        onClick={() => {
          offsetChanger(false);
        }}
      />
      <RightIcon
        onClick={() => {
          offsetChanger(true, false);
        }}
      ></RightIcon>
      <SliderContainer offset={offset} transition={transition}>
        <Slide style={{ background: `url(${Img5})` }}>
          <SlideTitle>Bezbedno do vas!</SlideTitle>
          <SlideSubtitle>
            Prodajem Kupujem obezbeđuje siguran prevoz bilo koje vrste robe u
            saradnji sa partnerskim kurirskim službama!
          </SlideSubtitle>
        </Slide>
        <Slide style={{ background: `url(${Img1})` }}>
          <SlideTitle>Dobrodošli na Prodajem Kupujem!</SlideTitle>
          <SlideSubtitle>
            Prvi i najveći sajt za oglašavanje na teritoriji Republike Srbije!
          </SlideSubtitle>
        </Slide>
        <Slide style={{ background: `url(${Img2})` }}>
          <SlideTitle>Upoznajte naš tim!</SlideTitle>
          <SlideSubtitle>
            Tim vrhunskih stručnjaka u svim granama industrije!
          </SlideSubtitle>
        </Slide>
        <Slide style={{ background: `url(${Img3})` }}>
          <SlideTitle>Prodajem Kupujem u Vašem dzepu!</SlideTitle>
          <SlideSubtitle>
            Od sada našu aplikaciju možete preuzeti na Vaš pametni telefon i
            postavljati oglase gde god da se nalazite!
          </SlideSubtitle>
        </Slide>
        <Slide style={{ background: `url(${Img4})` }}>
          <SlideTitle>Pouzdanost na prvom mestu!</SlideTitle>
          <SlideSubtitle>
            Najkvalitetniji hardver i strucči operatori obezbedjuju korisnicima
            Prodajem Kupujem neometano korišćenje našeg Web Sajta!
          </SlideSubtitle>
        </Slide>
        <Slide style={{ background: `url(${Img5})` }}>
          <SlideTitle>Bezbedno do vas!</SlideTitle>
          <SlideSubtitle>
            Prodajem Kupujem obezbeđuje siguran prevoz bilo koje vrste robe u
            saradnji sa partnerskim kurirskim službama!
          </SlideSubtitle>
        </Slide>
        <Slide style={{ background: `url(${Img1})` }}>
          <SlideTitle>Dobrodosli na Prodajem Kupujem!</SlideTitle>
          <SlideSubtitle>
            Prvi i najveći sajt za oglašavanje na teritoriji Republike Srbije!
          </SlideSubtitle>
        </Slide>
      </SliderContainer>
    </ImageCarousel>
  );
};

export default Hero;
