import {
  FormTextContainer,
  FormTextParagraph,
  FormTextTitle,
  NumberCircleIcon,
  StepContainer,
} from "./FormTextElements";

const FormText = () => {
  return (
    <FormTextContainer>
      <FormTextTitle>Kako do vašeg oglasa?</FormTextTitle>
      <StepContainer>
        <FormTextParagraph>
          Potrebno je da se registrujete besplatno na Prodajem Kupujem!
        </FormTextParagraph>
        <NumberCircleIcon number="1"></NumberCircleIcon>
      </StepContainer>
      <StepContainer>
        <NumberCircleIcon number="2"></NumberCircleIcon>
        <FormTextParagraph>
          Nakon registracije, potrebno je potvrditi Vašu email adresu!
        </FormTextParagraph>
      </StepContainer>
      <StepContainer>
        <FormTextParagraph>
          Popunite podatke o stvari koju prodajete u formi ispod!
        </FormTextParagraph>
        <NumberCircleIcon number="3"></NumberCircleIcon>
      </StepContainer>
      <StepContainer>
        <NumberCircleIcon number="4"></NumberCircleIcon>
        <FormTextParagraph>
          Klikom na dugme ispod Vaš oglas je postavljen i čeka na kupca!
        </FormTextParagraph>
      </StepContainer>
    </FormTextContainer>
  );
};

export default FormText;
