import styled from "styled-components";

export const FooterContainer = styled.section`
  min-height: 25vh;
  background: rgba(0, 51, 104, 1);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
`;

export const FooterLogo = styled.p`
  font-size: 1rem;
  color: white;
  text-align: center;
  @media screen and (max-width: 800px) {
    font-size: 0.8rem;
  }
`;

export const InformationContainer = styled.div`
  display: flex;
  width: 80%;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 0.5%;

  @media screen and (max-width: 800px) {
    flex-direction: column;
    height: 50vh;
  }
`;

export const FooterLocation = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-evenly;
  height: 15vh;
  color: white;
  text-align: left;
  font-weight: 300;

  @media screen and (max-width: 800px) {
    width: 80%;
    align-items: center;

    h1 {
      width: 90%;
    }
    div {
      width: 90%;
    }
  }
`;
export const FooterContact = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-evenly;
  height: 15vh;
  color: white;
  text-align: left;
  font-weight: 300;

  @media screen and (max-width: 800px) {
    width: 80%;
    align-items: center;
    h1 {
      width: 90%;
    }
    div {
      width: 90%;
    }
  }
`;
