import styled from "styled-components";
import { GoTriangleLeft, GoTriangleRight } from "react-icons/go";
import { AiFillStar } from "react-icons/ai";
import ImgBg from "../../Img/pozadina.png";

const kp_blue = "#003368";
const kp_red = "#FF1E00";

export const ReviewsContainer = styled.section`
  height: 45vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
  /* font-family: "Lato"; */
  width: 100%;
`;

export const ReviewsSliderContainer = styled.div`
  height: 40vh;

  display: flex;
  width: 100%;
`;

export const ReviewsSlideCointainer = styled.div`
  width: ${({ active }) => (active ? 100 : 0)}%;
  height: ${({ active }) => (active ? 100 : 0)}%;
  transition: opacity 0.5s ease-in;
  opacity: ${({ active }) => (active ? 100 : 0)}%;
  flex-shrink: 0;
`;

export const ReviewsSlide = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
`;

export const LeftIcon = styled(GoTriangleLeft)`
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
  color: rgba(0, 51, 104, 0.5);
  z-index: 5;
  font-size: 4rem;
  transition: 0.2s ease;

  &:hover {
    color: rgba(0, 51, 104, 0.8);
    cursor: pointer;
  }
  @media screen and (max-width: 800px) {
    font-size: 2rem;
  }
`;

export const RightIcon = styled(GoTriangleRight)`
  position: absolute;
  top: 50%;
  right: 5px;
  transform: translateY(-50%);
  color: rgba(0, 51, 104, 0.5);
  z-index: 5;
  font-size: 4rem;
  transition: 0.2s ease;

  &:hover {
    color: rgba(0, 51, 104, 0.8);
    cursor: pointer;
  }

  @media screen and (max-width: 800px) {
    font-size: 2rem;
  }
`;

export const ReviewTitle = styled.h1`
  font-size: 2rem;
  user-select: none;

  @media screen and (max-width: 800px) {
    font-size: 1.5rem;
  }
`;

export const StarContainer = styled.div`
  display: flex;
  align-items: center;
  width: 30%;
  justify-content: center;
  margin: 1% 0 2.5% 0;

  @media screen and (max-width: 800px) {
    width: 60%;
  }
`;

export const StarIcon = styled(AiFillStar)`
  color: #003368;
  font-size: 1.5rem;
  width: 7%;
`;

export const ReviewText = styled.p`
  color: rgba(0, 51, 104, 0.3);
  font-style: italic;
  font-size: 1.5rem;
  user-select: none;

  @media screen and (max-width: 800px) {
    font-size: 1rem;
    width: 80%;
    text-align: center;
  }
`;

export const ReviewContainerTitle = styled.h1`
  align-self: flex-end;
  margin: 0 10%;
  font-size: 2.5rem;
  position: relative;
  text-align: left;
  padding: 1% 0;

  &::after {
    content: "";
    width: 50%;
    height: 10px;
    background: linear-gradient(-90deg, ${kp_blue}, ${kp_red}), ${kp_red};
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 10px;
  }

  @media screen and (max-width: 800px) {
    font-size: 1.5rem;
    padding: 3% 0;
    font-weight: bold;
    align-self: flex-start;

    &::after {
      height: 8px;
      left: 0;
      top: 0;
    }
  }
`;
