import styled from "styled-components";
// import Img1 from "../../Img/img1.jpg";
import { GoTriangleRight, GoTriangleLeft } from "react-icons/go";

// const kp_blue = "#003368";
// const kp_red = "#FF1E00";

export const ImageCarousel = styled.div`
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 90vh;
  margin-top: 10vh;
`;

export const SliderContainer = styled.div`
  width: 100%;
  position: relative;
  height: 90vh;
  background: red;
  display: flex;
  /* overflow: hidden; */
  flex-shrink: 0;
  transform: translateX(${({ offset }) => offset}%);
  transition: ${({ transition }) => transition};
`;

export const Slide = styled.div`
  height: 100%;
  width: 100%;
  background-size: cover !important;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  padding: 10%;
`;

export const LeftIcon = styled(GoTriangleLeft)`
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
  color: rgba(255, 255, 255, 0.5);
  z-index: 5;
  font-size: 4rem;
  transition: 0.2s ease;

  &:hover {
    color: rgba(255, 255, 255, 0.8);
    cursor: pointer;
  }
`;

export const RightIcon = styled(GoTriangleRight)`
  position: absolute;
  top: 50%;
  right: 5px;
  transform: translateY(-50%);
  color: rgba(255, 255, 255, 0.5);
  z-index: 5;
  font-size: 4rem;
  transition: 0.2s ease;

  &:hover {
    color: rgba(255, 255, 255, 0.8);
    cursor: pointer;
  }
`;

export const SlideTitle = styled.h1`
  color: white;
  text-shadow: 3px 3px 5px rgba(0, 0, 0, 0.6);
  font-size: 3.5rem;
  color: white;

  @media screen and (max-width: 800px) {
    font-size: 2rem;
  }
`;

export const SlideSubtitle = styled.p`
  color: white;
  text-shadow: 2px 2px 3px rgba(0, 0, 0, 0.6);
  font-size: 1.5rem;
  font-style: italic;

  @media screen and (max-width: 800px) {
    font-size: 1rem;
  }
`;
