import { React, useState, useEffect } from "react";
import {
  LinkList,
  Nav,
  NavLogo,
  NavLink,
  NavText,
  Dropdown,
  HamburgerIcon,
  NavContainer,
} from "./NavbarElements";

const Navbar = () => {
  const [broj, postaviBroj] = useState(0);
  const [open, setOpen] = useState(false);
  const [background, setBackground] = useState(false);
  const zeljeni_br = Math.floor(Math.random() * 17000);
  let br = zeljeni_br + 5000;
  const { innerWidth: width } = window;

  function smanjiBroj() {
    let raz = br - zeljeni_br;
    br = br - Math.floor(Math.random() * (raz * 0.1 - raz * 0.01) + raz * 0.01);

    postaviBroj(br);

    if (br - 100 > zeljeni_br) {
      window.setTimeout(smanjiBroj, 10);
    }
  }

  useEffect(() => {
    smanjiBroj();
  }, []);

  function toggleDropdown() {
    setOpen(!open);
  }

  function navbarBackground() {
    if (window.scrollY > 0) {
      setBackground(true);
    } else {
      setBackground(false);
    }
  }

  window.addEventListener("scroll", navbarBackground);

  return (
    // style={{ position: "fixed", top: 0, width: "100%", zIndex: "100" }}
    <NavContainer background={background}>
      <Nav>
        <NavLogo>
          <span style={{ color: "#003368", display: "block" }}>PRODAJEM</span>
          KUPUJEM
        </NavLogo>
        <NavText>Trenutno postavljeno {broj} oglasa!</NavText>
        {width > 800 ? (
          <LinkList>
            <NavLink to="/">Početna</NavLink>
            <NavLink to="/create">Postavi Oglas</NavLink>
            <NavLink to="/about">O Nama</NavLink>
          </LinkList>
        ) : (
          <HamburgerIcon onClick={toggleDropdown}></HamburgerIcon>
        )}
      </Nav>
      <Dropdown open={open}>
        <NavLink onClick={toggleDropdown} to="/">
          Pocetna
        </NavLink>
        <NavLink onClick={toggleDropdown} to="/create">
          Postavi Oglas
        </NavLink>
        <NavLink onClick={toggleDropdown} to="/about">
          O Nama
        </NavLink>
      </Dropdown>
    </NavContainer>
  );
};

export default Navbar;
