import {
  FormElement,
  FormInput,
  FormSectionContainer,
  FormTitle,
  FormElementContainer,
  FormImage,
  FormLabel,
  FormInputContainer,
  FormError,
  FormSubmit,
  BorderDiv,
} from "./FormElements";
import ClipboardImg from "../../Img/clipboard.png";
import EnvelopeImg from "../../Img/envelope1.png";
import useForm from "./useForm";
import validate from "./formValidate";

const Form = () => {
  const { handleChange, values, handleSubmit, errors } = useForm(validate);
  return (
    <form method="POST" onSubmit={handleSubmit}>
      <FormSectionContainer>
        <FormTitle left={false}>Registracija</FormTitle>
        <FormElementContainer>
          <FormElement left={true}>
            <FormItem
              data={{
                handleChange: handleChange,
                values: values,
                code: "name",
                type: "text",
                placeholder: "Vaše Ime",
                isError: errors.name !== "" && errors.name,
                errMsg: errors.name,
              }}
            ></FormItem>
            <FormItem
              data={{
                handleChange: handleChange,
                values: values,
                code: "email",
                type: "text",
                placeholder: "Email",
                isError: errors.email !== "" && errors.email,
                errMsg: errors.email,
              }}
            ></FormItem>
            <FormItem
              data={{
                handleChange: handleChange,
                values: values,
                code: "password",
                type: "password",
                placeholder: "Lozinka",
                isError: errors.password !== "" && errors.password,
                errMsg: errors.password,
              }}
            ></FormItem>
            <FormItem
              data={{
                handleChange: handleChange,
                values: values,
                code: "password2",
                type: "password",
                placeholder: "Ponovite Lozinku",
                isError: errors.password2 !== "" && errors.password2,
                errMsg: errors.password2,
              }}
            ></FormItem>
          </FormElement>
          <FormImage src={EnvelopeImg}></FormImage>
        </FormElementContainer>

        <FormTitle left={true}>Informacije o oglasu</FormTitle>

        <FormElementContainer>
          <FormImage src={ClipboardImg}></FormImage>
          <FormElement left={false}>
            <FormItem
              data={{
                handleChange: handleChange,
                values: values,
                code: "title",
                type: "text",
                placeholder: "Naslov Oglasa",
                isError: errors.title !== "" && errors.title,
                errMsg: errors.title,
              }}
            />
            <FormItem
              data={{
                handleChange: handleChange,
                values: values,
                code: "desc",
                type: "text",
                placeholder: "Opis",
                isError: errors.desc !== "" && errors.desc,
                errMsg: errors.desc,
              }}
            />
            <FormItem
              data={{
                handleChange: handleChange,
                values: values,
                code: "price",
                type: "text",
                placeholder: "Cena",
                isError: errors.price !== "" && errors.price,
                errMsg: errors.price,
              }}
            />
          </FormElement>
        </FormElementContainer>
        <BorderDiv>
          <FormSubmit type="submit" value="Postavi Oglas"></FormSubmit>
        </BorderDiv>
      </FormSectionContainer>
    </form>
  );
};

export default Form;

const FormItem = ({ data }) => {
  return (
    <FormInputContainer>
      <FormLabel>{data.placeholder}</FormLabel>
      <FormInput
        onChange={data.handleChange}
        value={data.values[data.code]}
        id={data.code}
        name={data.code}
        type={data.type}
        error={data.isError}
      ></FormInput>

      {/* {data.isError ? <FormError>{data.errMsg}</FormError> : null} */}
      <FormError error={data.isError}>{data.errMsg}</FormError>
    </FormInputContainer>
  );
};
