import {
  FacebookIcon,
  InstagramIcon,
  YoutubeIcon,
  SocialButtonsContainer,
  SocialContainer,
  SocialContainerTitle,
  IconLink,
} from "./SocialElements";

const Social = () => {
  return (
    <SocialContainer>
      <SocialContainerTitle>Društvene mreže</SocialContainerTitle>
      <SocialButtonsContainer>
        <IconLink href="https://facebook.com" target="_blank">
          <FacebookIcon></FacebookIcon>
        </IconLink>
        <IconLink href="https://instagram.com" target="_blank">
          <InstagramIcon></InstagramIcon>
        </IconLink>
        <IconLink href="https://youtube.com" target="_blank">
          <YoutubeIcon></YoutubeIcon>
        </IconLink>
      </SocialButtonsContainer>
    </SocialContainer>
  );
};

export default Social;
