export default function (values) {
  let errors = {};

  //   Ime
  if (!values.name.trim()) {
    errors.name = "Morate uneti ime!";
  }

  //   Email
  if (!values.email.trim()) {
    errors.email = "Morate uneti email!";
  } else if (
    !/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
      String(values.email).toLowerCase()
    )
  ) {
    errors.email = "Adresa mora biti validna";
  }

  //   Password
  if (!values.password.trim()) {
    errors.password = "Morate uneti šifru";
  } else if (
    !/^(?=.*[0-9])(?=.*[a-z])([a-z0-9]{8})$/.test(String(values.password))
  ) {
    errors.password = "Šifra mora da sadrži: 8 karaktera i broj";
  }

  //   Confirm password
  if (!values.password2.trim()) {
    errors.password2 = "Morate uneti šifru!";
  } else if (values.password.trim() !== values.password2.trim()) {
    errors.password2 = "Šifre moraju da se podudaraju!";
  }

  //   Naslov oglasa
  if (!values.title.trim()) {
    errors.title = "Morate uneti ime oglasa!";
  }
  //   Deskripcija
  if (!values.desc.trim()) {
    errors.desc = "Morate uneti opis oglasa!";
  }

  if (!values.price.trim()) {
    errors.price = "Morate uneti cenu!";
  } else if (!/^[0-9]*$/.test(values.price.trim())) {
    errors.price = "Cena mora biti broj";
  }

  return errors;
}
