import styled from "styled-components";
import {
  RiFacebookCircleLine,
  RiInstagramLine,
  RiYoutubeLine,
} from "react-icons/ri";

const kp_blue = "#003368";
const kp_red = "#FF1E00";

export const SocialContainer = styled.section`
  display: flex;
  height: 30vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
`;

export const SocialContainerTitle = styled.h1`
  align-self: flex-start;
  margin: 0 10%;
  font-size: 2.5rem;
  position: relative;
  text-align: left;
  padding: 1% 0;

  &::after {
    content: "";
    width: 50%;
    height: 10px;
    background: linear-gradient(-90deg, ${kp_blue}, ${kp_red}), ${kp_red};
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 10px;
  }
  @media screen and (max-width: 800px) {
    font-size: 1.5rem;
    padding: 3% 0;
    font-weight: bold;

    &::after {
      height: 8px;
      left: 0;
      top: 0;
    }
  }
`;

export const SocialButtonsContainer = styled.div`
  width: 50%;
  height: 30vh;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
`;

export const IconLink = styled.a`
  margin: 0 0;
  transition: 0.1s ease-in;
  font-size: 6rem;
  color: rgba(0, 51, 104, 0.5);

  &:hover {
    transform: scale(114%);
  }

  @media screen and (max-width: 800px) {
    font-size: 3rem;
    margin: 0 8%;
  }
`;

export const FacebookIcon = styled(RiFacebookCircleLine)`
  cursor: pointer;
`;
export const InstagramIcon = styled(RiInstagramLine)`
  cursor: pointer;
`;
export const YoutubeIcon = styled(RiYoutubeLine)`
  cursor: pointer;
`;
