import styled from "styled-components";
import { BsCircle } from "react-icons/bs";

const kp_blue = "#003368";
const kp_red = "#FF1E00";

export const FormTextContainer = styled.section`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
`;

export const FormTextTitle = styled.h1`
  align-self: flex-start;
  color: black;
  margin: 0 10%;
  font-size: 2.5rem;
  position: relative;
  text-align: left;
  padding: 1% 0;

  &::after {
    content: "";
    width: 50%;
    height: 10px;
    background: linear-gradient(-90deg, ${kp_blue}, ${kp_red}), ${kp_red};
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 10px;
  }

  @media screen and (max-width: 800px) {
    font-size: 1.5rem;
    padding: 3% 0;
    font-weight: bold;

    &::after {
      height: 8px;
      left: 0;
      top: 0;
    }
  }
`;

export const FormTextParagraph = styled.p`
  /* width: 90%; */
  color: ${kp_blue};
  font-size: 2rem;
  line-height: 2.3rem;
  color: black;
  font-weight: 300;

  @media screen and (max-width: 800px) {
    font-size: 1.2rem;
    line-height: 1.4rem;
    width: 70%;
  }
`;

export const NumberCircleIcon = styled.h3`
  font-size: 4rem;
  position: relative;
  height: 6rem;
  width: 6rem;
  z-index: 5;
  text-align: center;

  &::before {
    content: "";
    width: 100%;
    height: 100%;
    background: linear-gradient(-90deg, ${kp_red}, ${kp_blue});
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    z-index: 3;
  }
  &::after {
    content: "${({ number }) => number}";
    width: 80%;
    height: 80%;
    background: #f6f8f8;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    z-index: 3;
  }

  @media screen and (max-width: 800px) {
    font-size: 2rem;
    height: 3rem;
    width: 3rem;
  }
`;

export const StepContainer = styled.div`
  display: flex;
  width: 60%;
  height: 15vh;

  justify-content: space-evenly;
  align-items: center;

  @media screen and (max-width: 800px) {
    width: 80%;
  }
`;
