import styled from "styled-components";

const kp_blue = "#003368";
const kp_red = "#FF1E00";

export const AboutContainer = styled.section`
  height: 100vh;
  width: 100%;
  display: flex;
  background: #f6f8f8;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  font-family: "Roboto";
  /* color: #212121; */
  color: rgba(0, 51, 104, 1);
  /* color: black; */
`;

export const AboutTitle = styled.h1`
  align-self: flex-start;
  color: black;
  margin: 0 10%;
  font-size: 2.5rem;
  position: relative;
  text-align: left;
  padding: 1% 0;

  &::after {
    content: "";
    width: 50%;
    height: 10px;
    background: linear-gradient(-90deg, ${kp_blue}, ${kp_red}), ${kp_red};
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 10px;
  }

  @media screen and (max-width: 800px) {
    font-size: 1.5rem;
    padding: 3% 0;
    font-weight: bold;

    &::after {
      height: 8px;
      left: 0;
      top: 0;
    }
  }
`;

export const AboutText = styled.p`
  width: 60%;
  text-align: center;
  font-size: 1.25rem;
  line-height: 3rem;
  margin: 2% 0 2% 0;

  @media screen and (max-width: 800px) {
    font-size: 1rem;
    line-height: 1.5rem;
    width: 80%;
    margin: 10% 0 10% 0;
  }
`;

export const AboutGallery = styled.div`
  display: flex;
  flex-wrap: wrap;
  flex-shrink: 0;
  justify-content: center;
  width: 70%;
  height: 50vh;
  @media screen and (max-width: 800px) {
    height: 30vh;
    width: 90%;
  }
`;

export const AboutImg = styled.img`
  width: ${({ width }) => width}%;
  height: ${({ height }) => height}%;
  object-fit: cover;
  padding: 0.3%;
`;
