import {
  ReviewsContainer,
  ReviewsSlide,
  ReviewsSliderContainer,
  LeftIcon,
  RightIcon,
  ReviewsSlideCointainer,
  ReviewTitle,
  StarContainer,
  StarIcon,
  ReviewText,
  ReviewContainerTitle,
} from "./ReviewComponents";
import { slides } from "./ReviewsData";
import { useState } from "react";

const Reviews = () => {
  const [counter, setCounter] = useState(0);
  const length = slides.length;

  function prevSlide() {
    setCounter(counter === 0 ? length - 1 : counter - 1);
  }

  function nextSlide() {
    setCounter(counter === length - 1 ? 0 : counter + 1);
  }

  return (
    <ReviewsContainer>
      <ReviewContainerTitle>Šta kažu o nama?</ReviewContainerTitle>
      <LeftIcon onClick={prevSlide}></LeftIcon>
      <RightIcon onClick={nextSlide}></RightIcon>
      <ReviewsSliderContainer>
        {slides.map((slide, index) => {
          return (
            <ReviewsSlideCointainer active={index === counter} key={index}>
              {counter === index ? (
                <ReviewsSlide color={slide.color}>
                  <ReviewTitle>{slide.name}</ReviewTitle>
                  <StarContainer>
                    {slide.stars.map((star, index) => {
                      return <StarIcon key={index}></StarIcon>;
                    })}
                  </StarContainer>
                  <ReviewText>❝ {slide.review} ❞</ReviewText>
                </ReviewsSlide>
              ) : (
                <></>
              )}
            </ReviewsSlideCointainer>
          );
        })}
      </ReviewsSliderContainer>
    </ReviewsContainer>
  );
};

export default Reviews;
